import React, {useCallback, useEffect} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {getAllBlogs} from "../../../redux/action/mainBlog";

import BlogItem from "../../global/BlogItem/BlogItem";
import SecondaryBtn from "../../layout/SecondaryBtn/SecondaryBtn";

import styles from "./BlogList.module.scss"
import {loadingLottie} from "../../../assets/lotties";
import Lottie from "lottie-react";


function BlogList() {
    const {data,nextPage,currPage} = useSelector(state => state.mainBlog)
    const dispatch = useDispatch()

    useEffect(() => {
        dispatch(getAllBlogs())
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const onClickSeeMore = useCallback(
        () => {
        // eslint-disable-next-line
        dispatch(getAllBlogs(null,currPage + 1))
    },
        // eslint-disable-next-line
        [currPage])

    return (
        <section className={`${styles['blogList']} container`}>
            {data?.length ?
                <div className={styles["blogList__wrapper"]}>
                    {data.map((item, index) => (
                        <BlogItem
                            title={item?.title}
                            key={item?.id}
                            description={item?.description}
                            date={item?.date}
                            image={item?.image}
                            id={item?.id}
                        />
                    ))}
                </div>
                :
                <Lottie
                    animationData={loadingLottie}
                    loop={true}
                    className={styles["blogList__loader"]}
                />
            }
            {nextPage && <SecondaryBtn onClick={onClickSeeMore} className={styles['blogList__btn']}>See More</SecondaryBtn>}

        </section>
    );
}

export default BlogList;